import utils from '../wxs/utils.js';
import youField from '@/components/field/index.vue';
export default {
	name: 'youSearch',
	components: {youField},
	props: {
		label: String,
		focus: Boolean,
		error: Boolean,
		disabled: Boolean,
		readonly: Boolean,
		inputAlign: String,
		showAction: Boolean,
		useActionSlot: Boolean,
		useLeftIconSlot: Boolean,
		useRightIconSlot: Boolean,
		leftIcon: {
			type: String,
			default: 'search'
		},
		rightIcon: String,
		placeholder: String,
		placeholderStyle: String,
		actionText: {
			type: String,
			default: '取消'
		},
		background: {
			type: String,
			default: '#ffffff'
		},
		maxlength: {
			type: Number,
			default: -1
		},
		shape: {
			type: String,
			default: 'square'
		},
		clearable: {
			type: Boolean,
			default: true
		}
	},
	computed:{
		search:function(){
			let showAction = this.showAction;
			let useActionSlot = this.useActionSlot;
			return utils.bem('search', { withaction: showAction || useActionSlot });
		},
		search__content:function(){
			let shape = this.shape;
			return utils.bem('search__content', [shape]);
		}
	},
	methods: {
		onChange(event) {
			this.value = event.detail;
			this.$emit('change', event.detail);
		},
		onCancel() {
			/**
			 * 修复修改输入框值时，输入框失焦和赋值同时触发，赋值失效
			 * https://github.com/youzan/@you/weapp/issues/1768
			 */
			setTimeout(() => {
				this.value = '';
				this.$emit('cancel');
				this.$emit('change', '');
			}, 200);
		},
		onSearch() {
			this.$emit('search', this.value);
		},
		onFocus() {
			this.$emit('focus');
		},
		onBlur() {
			this.$emit('blur');
		},
		onClear() {
			this.$emit('clear');
		},
	}
}
